@import '../../../styles/variables';

.container {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100vh;
    background: $translucent-field;
    background-image: url('../../../assets/quiz-round-first.jpg');
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;

    .roundContent {
        margin: 0;
        padding: 40px;
        position: absolute;
        top: 10%;
        bottom: 10%;
        left: 5%;
        right: 5%;
        background: $white-background;
        backdrop-filter: blur(7px);
        box-shadow: 0 4px 30px #0000003f;
        border-radius: 25px;
        
        .header {
            display: flex;
            flex-direction: row;
            gap: 60px;
            align-items: center;
            justify-content: space-between;

                .roundNumber {
                    margin: 15px;
                    background-color: $background-pink;
                    border-radius: 15px;
                    width: 220px;
                    text-align: center;
                    font-family: $main-font;
                    font-size: 32px;
                    color: $main-font-color;
                }

                .roundName {
                    margin: 15px;
                    text-align: center;
                    font-family: $accent-font;
                    font-size: 45px;
                    color: $accent-font-color-pink;
                    text-shadow: 2px 4px #56274d2d;
                }

                .questionNumber {
                    margin: 15px;
                    background-color: $background-pink;
                    border-radius: 15px;
                    width: 220px;
                    text-align: center;
                    font-family: $main-font;
                    font-size: 32px;
                    color: $main-font-color;
                }
            }

            .questions {
                margin: 0;

            }        
    }

    
}